/* person_card */

.card_style{
    /* height: 18rem; */
    display: block;
    margin: auto;
    margin-top: 2rem;
    background: #FFFFFF;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding:2%;

}
.card_display{
    height: 15rem;
    display: flex;
    justify-content:space-between;
    align-items: center;
}


 .card_image img{
    border-radius: 50%;
    padding-right:10%;
}

/* all_cards */

.all_cards{
    display: flex;
    flex-flow: row wrap;
    margin-top: 5rem;
    
   
}



.card_width{
    display: block;
    margin:0.5rem auto;
    width: 24% ;
    box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.2);
border-radius: 4px;
}

.card:hover{
    background-color: rgb(244, 246, 247) !important;
    transition: all 500ms ease;
}

